<template>
  <a-list item-layout="horizontal" :data-source="websiteList">
    <template #renderItem="{ item }">
      <a-list-item>
        <a-list-item-meta :description="item.webDesc">
          <template #title>
            <a :href="item.webUrl" target="_blank">{{ item.webName }}</a>
            <a-tag
              :color="colors[Math.floor(Math.random() * colors.length)]"
              style="margin-left: 5px"
              v-for="tag in item.tagList"
              :key="tag"
            >
              {{ tag }}
            </a-tag>
          </template>
          <template #avatar>
            <a-avatar :src="item.webAvatar" />
          </template>
        </a-list-item-meta>
        <a-button
          type="primary"
          style="background: pink"
          @click="toDetail(item.id)"
          >查看详情</a-button
        >
      </a-list-item>
    </template>
  </a-list>
</template>
<script setup lang="ts">
import { withDefaults, defineProps, toRefs, ref } from "vue";
import router from "@/router";

interface Props {
  websiteList: any[];
}

const props = withDefaults(defineProps<Props>(), {
  websiteList: () => [],
});

const toDetail = (id: number) => {
  router.push({
    path: "/detail",
    query: {
      id: id,
    },
  });
};

const colors = ["pink", "red", "green", "orange", "cyan", "blue", "purple"];
</script>
