<template>
  <div class="index-page">
    <a-input-search
      v-model:value="searchText"
      placeholder="请输入要搜索的关键词"
      enter-button="Search"
      size="large"
      @search="onSearch"
    />
    <MyDivider />
    <a-tabs v-model:activeKey="activeKey" @change="onTabChange">
      <a-tab-pane key="website" tab="网站">
        <WebsiteList :website-list="websiteList" />
      </a-tab-pane>
    </a-tabs>
  </div>
  <a-pagination
    v-model:current="current"
    :total="totalNum"
    show-less-items
    @change="updatePage"
  />
</template>

<script setup lang="ts">
