const route = useRoute();
const activeKey = route.params.category as string;
const router = useRouter();

const searchText = ref(route.params.text);

const initSearchParams = {
  text: "",
  type: "website",
  pageSize: 10,
  pageNumber: 1,
};
const postList = ref([]);

const userList = ref([]);

const pictureList = ref([]);

const websiteList = ref([]);

const totalNum = ref();
/**
